      <ContentBlock isCommentable={true} type='paragraph' children='Во время сборки колонны каждая труба должна быть отцентрирована относительно муфты и оси скважины. Перед началом свинчивания необходимо проверить на ощупь отсутствие механических повреждений уплотнительных и упорных поверхностей на свободном конце трубы. Защитные колпаки с ниппельной части удаляются непосредственно перед посадкой в муфту, в это же время на ниппельную часть наносится смазка. При свинчивании соединений необходимо применять резьбовую смазку. Расход смазки на одно соединение согласно рекомендациям компании-изготовителя резьбовой смазки. Резьбовая смазка должна распределяться между муфтой и ниппелем: 2/3 количества - на муфту, 1/3 – на ниппель. Смазку необходимо наносить на поверхности равномерно. Смазку наносить на высушенную поверхность резьбового соединения кистью, либо нейлоновой щеткой. Смазку перед применением тщательно перемещать, смазка должна быть однородно, иметь консистенцию мази, не содержать твердых включений. Запрещается свинчивание резьб без смазки, а также применение машинного, дизельного масла в качестве заменителей.' />
