      <ContentBlock isCommentable={true} type='paragraph' children='Дальнейшее свинчивание труб производится по установленному среднеарифметическому значению крутящего момента, полученного при проведении свинчивания десяти труб, следующих друг за другом. Полученные среднеарифметические значения крутящего момента являются оптимальными для данной сборки колонны. Минимальный крутящий момент, при этом, должен быть не менее 75% от принятого, а максимальный, не более 125%. Если при свинчивании происходит биение верхнего конца обсадной трубы, то следует снизить скорость вращения, чтобы не допустить пластической деформации резьбы.' />
