      <ContentBlock isCommentable={true} type='informer' children='Плановые значения параметров режима бурения, весов на крюке, крутящего момента на ВСП и эквивалентной циркуляционной плотности рассчитываются подрядчиком по ННБ и растворным подрядчиком при проектировании скважины в специализированном программном обеспечении и заносятся в режимную карту бурения. Фактические значения данных параметров заносятся в режимную карту бурильщиком в процессе бурения секции. Фактические значения эквивалентной циркуляционной плотности заносятся инженером по ННБ при условии наличия в КНБК датчика затрубного давления. Супервайзеру Заказчика отправить заполненную режимную карту по электронной почте инженеру – куратору кустовой площадки в течении суток после окончания бурения секции.' />
