      <ContentBlock isCommentable={true} type='paragraph' children='Спуск первых 10 (десяти) обсадных труб производить на двух элеваторах. Без присутствия супервайзера Заказчика сборка компоновки низа обсадной колонны запрещена. В случае наличия каких-либо затруднений (невозможность наворота, повреждение резьбовых соединений, выявление дефектов, несовпадение меток после полного свинчивания и др.) при сборке низа обсадной колонны, связанных с формированием промысловых соединений, ответственный ИТР Подрядчика по бурению совместно с супервайзером Заказчика принимает решение об отбраковке элементов технологической оснастки и обсадных труб.' />
