      <ContentBlock isEditable={true} type='paragraph' children='Обсадные трубы укладывать муфтовой частью в направлении буровой установки с учетом очередности спуска их в скважину. Снять защитные колпаки с ниппелей и муфт, очистить резьбовые соединения. Очистку от смазки проводить ветошью при помощи растворителя, не содержащего хлор, либо пароочистителем (ППУ). Не допускается использовать для удаления смазки дизельное топливо, соленую воду, барит и металлические щетки. После удаления смазки, резьбовые соединения следует тщательно протереть сухой и чистой ветошью или просушить продувкой сжатым воздухом. Осмотреть резьбовые соединения ниппеля и муфт. Трубы с поврежденными резьбами к спуску не допускаются. Измерить длину каждой трубы. Измерения производят от свободного торца муфты до того места трубы с наружной резьбой, которое соответствует положению торца муфты при закреплении гидравлическим ключом, для ОТТГ - последняя риска резьбы. Промаркировать (номер трубы в порядке спуска, длина) светлой краской или мелом, сформировать реестр спуска обсадной колонны. Произвести шаблонирование обсадных труб на стеллажах. Шаблонирование произвести внутренним шаблоном по всей длине труб. В случае если внутренний шаблон не проходит через обсадную трубу – отбраковать. Установить защитные колпаки. Перед наворотом убедиться в чистоте защитных колпаков и отсутствии на них посторонних предметов. Не допускается использование защитных колпаков со значительными повреждениями резьбы и формы. [Трубы обсадные. Руководство по эксплуатации. 2015г. ООО «Научно-исследовательский институт разработки и эксплуатации нефтепромысловых труб»].' />
