          'подготовить переходной (совместимый) адаптер «ОКК (ответный фланец) – ТБПК 127 мм (З-133)» (проверить целостность резьбовых соединений, очистить, нанести резьбоуплотняющую смазку);',
          'навернуть адаптер на ТБПК-127 мм;',
