          'произвести докрепление ОКК и «нулевого» патрубка с моментом 9 кН*м (момент свинчивания должен быть скорректирован по фактическому моменту свинчивания обсадных труб при спуске ОК-324мм), создание крутящего момента производить путем вращения ВСП, докрепление производить плавно без рывков с шагом по 0,5 кН*м, обеспечить фиксацию момента на диаграммах (диаграмму момента свинчивания предоставить в отдел инжиниринга бурения);',
