    emptyContentReadable: (<React.Fragment>
      <ContentBlock isCommentable={true} type='paragraph' />
    </React.Fragment>),
    contentEditable: (<React.Fragment>
      <ContentBlock isEditable={true} type='paragraph' children='Собрать роторную КНБК для бурения тех. кармана L – 30 м: Долото 393,7 мм + УБТ 203 – 9 м + СБТ 127 – остальной. Пробурить тех. карман для сборки КНБК для бурения при бурении секции 393,7 мм необходимо соблюдать следующие режимы бурения: 0-25 м расход – 15 л/с, обороты ВСП 80 – 100 об/мин.' />
      <ContentBlock isEditable={true} type='paragraph' children='Убедиться, что весь необходимый инструмент и оборудование, необходимые для строительства секции, находятся на буровой, проверены и работоспособны, имеют соответствующие документы (паспорта). Перед началом бурения обеспечить наличие бурового раствора на поверхности в объеме 105 м3. Собрать КНБК для бурения секции 393,7 мм согласно настоящей программы, тестирование оборудования для ННБ не производить (протокол ПТ-10-01/011 от 22.11.17). Поддерживать температуру бурового раствора на минимальном уровне, отключив подогрев всех емкостей. Бурение секции в интервале 0 - 460 м производить без проработки перед наращиванием. «Снятие пружины» перед снятием замеров в статике производить на длину одной трубы (12 метров). В случае наличия признаков зашламованности ствола скважины (скачки давления и крутящего момента на ВСП, несоответствие весов на крюке с расчетными на величину более 10%), произвести промывку ствола скважины и продолжить бурение с проработкой ствола скважины перед наращиванием. Расхаживание производить с вращением бурильного инструмента с частотой при движении «вверх» 40 об/мин, при движении «вниз» 60 об/мин. Фактический забой секции выбрать исходя из фактической длины обсадной колонны (с учетом технологической оснастки) – ЗУМПФ не должен превышать 3 м. После окончания бурения секции произвести прокачку очищающей пачки ВУС по рецептуре подрядчика по буровым растворам в объёме не менее 8 м3 с промывкой ствола скважины с расхаживанием и вращением бурильного инструмента до момента выхода пачки на устье скважины. Режим промывки: расход промывочной жидкости 65 л/сек, частота вращения ВСП 60 об/мин. Произвести подъём КНБК до глубины 50 м со сплошной проработкой. Режим проработки: в интервале 460-170 м расход промывочной жидкости 65 л/сек, частота вращения ВСП 40 об/мин; в интервале 170 – 50 м расход промывочной жидкости 50 л/сек, частота вращения ВСП 40 об/мин, скорость проработки ствола скважины не более 500 м/час. В интервале перехода «пески – глина» (подъем произвести на 50 метров выше зоны перехода), произвести спуск на 50 метров ниже интервала перехода без циркуляции и вращения. В случае получения посадки проработать до свободного хождения «на сухую».' />
      <ContentBlock isEditable={true} type='paragraph' children='В случае свободного хождения продолжить подъем сплошной проработкой до устья. После подъема произвести сборку роторную КНБК 660,4 мм, для шаблонировки ствола в интервале 0-50 м. Спустить КНБК до глубины исходя из следующего расчета: 1 м + 4 трубы термокейс 530 х 324*9,5 + высота стола ротора. В случае свободного прохождения КНБК в интервале 0-50 м произвести подъем и разборку КНБК. При отрицательном результате (спуск с посадками свыше 5 тн) проработать интервал до свободного хождения «на сухую». Режим проработки: расход промывочной жидкости 20 л/сек, частота вращения ВСП 40 об/мин.' />
      <ContentBlock isEditable={true} type='paragraph' children='В случае осложнения при подготовке ствола оповестить РН по планированию бурения. Произвести подъем и разборку КНБК, места затяжек проработать до свободного хождения КНБК без циркуляции. С целью недопущения зарезки нового ствола, механическая скорость проработки 75 - 100 м/час.' />
    </React.Fragment>),
